// 店铺动态相关接口
import axios from './../fetch'

// 店铺动态列表
export const shopDynamicList = params => { return axios.get('/admin/platform/storeDynamicRecord/page', { params: params }) }

// 删除店铺动态
export const shopDynamicDelete = params => { return axios.post('/admin/platform/storeDynamicRecord/deleteIds', params) }

// 店铺动态修改
export const shopDynamicUpdate = params => { return axios.post('/admin/platform/storeDynamicRecord/update', params) }



