<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>显示商家发布的公告信息</li>
      <li>可针对商家发布的不符合规定的公告强制删除及禁止显示</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>发布时间</label>
        <cms-picker @change="setTimer" value-format="yyyy-MM-dd HH:mm:ss"></cms-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
      <section class="cms-batch">
        <cms-button
          icon="error"
          v-prem="'/shopDynamic/delete'"
          type="error"
          size="small"
          @click="deleteDatas()"
        >批量删除</cms-button>
      </section>
    </cms-sticky>
    <el-table
      :data="tableData"
      style="width: 100%"
      stripe
      v-loading="loading"
      @selection-change="getCheckItems"
      @row-click="checkRows"
      ref="dataTable"
    >
      <el-table-column type="selection" width="46" fixed="left"></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="store.storeName" label="店铺名称" min-width="120"></el-table-column>
      <el-table-column prop="content" label="公告内容" min-width="160"></el-table-column>
      <el-table-column prop="storeUsername" label="发布时间" min-width="160">
        <template slot-scope="scope">{{$moment(scope.row.createTime)}}</template>
      </el-table-column>
      <el-table-column prop="isShow" label="显示" width="110">
        <div slot-scope="scope">
          <cms-switch
            v-model="scope.row.isShow"
            :id="scope.row.id"
            @change="show(scope.row.id,scope.row.isShow)"
          ></cms-switch>
        </div>
      </el-table-column>
      <el-table-column label="操作" width="175" fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button
            icon="error"
            v-prem="'/shopDynamic/delete'"
            size="small"
            type="info"
            @click.stop="deleteData(scope.row.id)"
          >删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  shopDynamicList,
  shopDynamicDelete,
  shopDynamicUpdate
} from '@/api/shop/shopDynamic.js'
var defaultData = {
  storeName: '',
  startTime: '',
  endTime: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    show(id, status) {
      // 修改显示的状态
      shopDynamicUpdate({ id: id, isShow: status }).then(res => {
        if (res.code === 200) {
          this.$successMessage('修改成功')
          this.getTableData(shopDynamicList, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(shopDynamicDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(shopDynamicDelete, this.ids) // 这个才是真实的删除方法
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    }
  },
  created() {
    // 首次触发
    this.getTableData(shopDynamicList, this.queryParams)
  }
}
</script>

<style>
</style>



